import React, { useState } from 'react';
import { Collapse } from 'antd'
import { Typography } from '@mui/material';
import './App.css';

function App() {
  const [date, setDate] = useState('');
  const [gender, setGender] = useState('')
  const [activeKey, setActiveKey] = useState('1')
  const [input, setInput] = useState(''); // 
  const [think, setThink] = useState('');
  const [fortune, setFortune] = useState(''); // 
  const [loading, setLoading] = useState(false); // 

  const streamDeepSeek = async (prompt, callback) => {
    setThink('')
    setFortune('')
    setLoading(true)
    try {
      const response = await fetch('https://qianfan.baidubce.com/v2/chat/completions', {
        method: 'POST',
        headers: {
          Authorization: 'Bearer bce-v3/ALTAK-6zsSZn5jbbt9EFkzBK5y3/7c36466da019b94d8b49e2421196759f879db75f;',
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          model: 'deepseek-r1',
          prompt: `${input}${gender}${date}`,
          messages: [{ role: 'user', content: `${input}${gender}${date}` }],
          stream: true,
        })
      });

      const reader = response?.body?.getReader();
      const decoder = new TextDecoder();

      while (true) {
        const { done, value } = await reader?.read() ?? {};
        if (done) {
          setLoading(false)
          break
        };
        const chunk = decoder.decode(value);
        try {
          const jsonStr = String(chunk).replace('data: ', '');
          const parsed = JSON.parse(jsonStr);

          callback(parsed.choices?.[0]?.delta?.reasoning_content, parsed.choices?.[0]?.delta?.content);  //  
        } catch (e) {
          console.error('Chunk :', e);
        }
      }
    } catch (error) {
      if (error?.name !== 'AbortError') {
        console.error(' :', error);
        setLoading(false)
      }
    }
  };

  // 
  const handleSubmit = async (e) => {
    e.preventDefault();
    if (input.trim() && date.trim() && gender.trim()) {
      let fullResponse = '';
      let fullThink = ''

      await streamDeepSeek({
        name: input,
        date: date,
      }, (think, text) => {
        if (think) {
          fullThink += think
          setThink(fullThink);
        }

        if (text) {
          fullResponse += text;
          setFortune(fullResponse); //  
        }

      }, false);

    }
  };

  return (
    <div className="App">
      <h1></h1>
      <p>YYYY-MM-DD</p>

      <form onSubmit={handleSubmit}>
        <input
          type="text"
          value={gender}
          onChange={(e) => setGender(e.target.value)}
          placeholder=""
          disabled={loading}
        />
        <input
          type="text"
          value={input}
          onChange={(e) => setInput(e.target.value)}
          placeholder=""
          disabled={loading}
        />
        <input
          type="text"
          value={date}
          onChange={(e) => setDate(e.target.value)}
          placeholder=""
          disabled={loading}
        />
        <button type="submit" disabled={loading}>
          {loading ? '...' : ''}
        </button>
      </form>

      {(think || fortune) && (
        <div className="fortune-result">
          <h2> <span style={{ fontSize: 16 }}>()</span></h2>
          <Collapse
            activeKey={activeKey}
            onChange={setActiveKey}
            bordered={false}
            items={[{ key: '1', label: <p className='think-title'></p>, children: <p className='think-text'>{think}</p> }]}
          />
          <div className='result-content'>
            <Typography className="response-box" variant="body1" sx={{ mt: 2, whiteSpace: 'pre-wrap' }}>
              {fortune}
            </Typography>
          </div>
        </div>
      )}
    </div>
  );
}

export default App;
